.image-pattern-style {
    background-color: white;
    background-size: contain;
    background-repeat: no-repeat;
    height: 50px;
    width: calc((100% - 18px) / 3);
    cursor: pointer;
    border: 1px solid #d5d5d5;
    background-position: center;
    float: left;
  }
  
  .image-pattern-style:hover {
    border-color: gray;
    border-width: 2px;
  }
  
  .e-remove-selection {
    pointer-events: none;
  }
  
  .column-style {
    display: table;
    height: 35px;
    padding-right: 4px;
    padding-left: 0px;
    width: calc((100% - 12px) / 3);
  }
  
  .row {
    margin-left: 0px;
    margin-right: 0px;
  }
  
  .row-header {
    font-size: 15px;
    font-weight: 500;
  }
  
  .e-checkbox-wrapper .e-label {
    font-size: 12px;
  }
  
  .e-selected-style {
    border-color: #006ce6;
    border-width: 2px;
  }
  
